.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  grid-column-gap: 20px;

  &__item {
    justify-self: center;
    &:hover {
      // transform: rotate(90deg);
    }
  }
}

.img {
  height: 250px;
  width: 250px;
  object-fit: contain;
  object-position: center;
  margin: 20px;
  transition: all 0.5s;

    @media (min-width: 768px) {
      height: 360px;
      width: 360px;
    }
    @media (min-width: 1200px) {
      height: 380px;
      width: 380px;
    }

  &:hover {
    transform: scale(1.1);

  }
}